export default {
	data() {
		return {
			isLoading: true,
			teamMsg: [],
			totalNumber: null,
			modelName: '',
			name: '',
			phone: '',
			isNull: false,
			takeNumber: '',
			page: 1,
			stopLoad: false,
			codePic: null,
			stores: [],
			keywords: "",
			stopLoadShops: false
		}
	},
	onShow() {
		this.getShopTeam()
		this.getStoreList()
	},
	methods: {
		toShopTeam(shop_id, shop_name) {
			uni.setStorageSync('shop_id', shop_id)
			uni.navigateTo({
				url: "./shopTeam?shop_name=" + shop_name
			})
		},
		searchShop() {
			this.page = 1
			this.stopLoadShops = false
			this.getStoreList()
		},
		getShopTeam() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/userCenter/myteam",
				data: {
					token: uni.getStorageSync('token')
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.totalNumber = res.data.data.count
						return
					}
					this.showToast(res.data.msg)
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		getStoreList() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/UserCenter/getStoreList",
				data: {
					token: uni.getStorageSync('token'),
					keywords: this.keywords,
					page: 1
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					this.isLoading = false
					if (res.data.status == 0) {
						this.stores = res.data.data.data
						return
					}
					this.showToast(res.data.msg)

				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		//触底加载更多所属门店
		loadMoreShops() {
			if (this.stopLoadShops) {
				return
			}
			this.shopPage++
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/UserCenter/getStoreList",
				data: {
					token: uni.getStorageSync('token'),
					keywords: this.keywords,
					page: this.shopPage
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if (res.data.data.length == 0) {
							this.stopLoadShops = true
							return
						}
						for (let i = 0; i < res.data.data.length; i++) {
							this.stores.push(res.data.data[i])
						}
						return
					}
					this.showToast(res.data.msg)
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		clearWords(){
			this.keywords = ''
			this.searchShop()
		},
		takePhone(num) {
			this.takeNumber = num
			this.modelName = 'phoneModel'
		},
		closeModel() {
			this.modelName = ''
		},
		clearTxt(index) {
			if (index == 1) {
				this.name = ''
			} else {
				this.phone = ''
			}
		},
		showModel() {
			this.modelName = 'addMemberModel'
		},
		sureTakePhone() {
			uni.makePhoneCall({
				phoneNumber: this.takeNumber
			});
			this.closeModel()
		},
		toFriendRecord() {
			uni.navigateTo({
				url: "../friendRecord/friendRecord"
			})
		},
		toMemberDetail(user_id, shop_id) {
			uni.navigateTo({
				url: "../memberMessage/memberMessage?user_id=" + user_id + "&shop_id=" + shop_id
			})
		},
		toCode() {
			console.log('生成二维码')
			uni.navigateTo({
				url: './codeShare'
			})
		},
		//处理下单时间
		handleTime(time) {
			let date = new Date(time * 1000)
			let Y = date.getFullYear().toString()
			let M = (date.getMonth() + 1).toString().padStart(2, '0')
			let D = date.getDate().toString().padStart(2, '0')
			let h = date.getHours().toString().padStart(2, '0')
			let m = date.getMinutes().toString().padStart(2, '0')
			let s = date.getSeconds().toString().padStart(2, '0')
			let newTime = Y + '-' + M + '-' + D + ' ' + h + ':' + m
			return newTime
		},
	}
}
